$main-color: #1eacc7;
$second-color: #ff5656;

* {
    font-family: 'Roboto', sans-serif; }

header {
    position: relative;
    height: 50px; }

.label {
    margin: 0 auto;
    width: 112px;
    height: 24px;
    padding: 5px 12px;
    background-color: #000000;
    color: #ffffff;
    font-size: 11px;
    font-weight: 400;
    text-transform: uppercase;
    text-align: center; }

.title {
    margin-bottom: 0;
    margin-top: 38px;
    color: #222222;
    font-size: 38px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center; }

.subtitle {
    margin-top: 19px;
    color: #222222;
    font-size: 17px;
    font-weight: 400;
    text-align: center; }

nav {
    position: fixed;
    width: 100%;
    height: 50px;
    background-color: #212121;
    z-index: 2; }

.menu {
    display: flex;
    justify-content: space-between;
    padding: 13px 70px;
    margin: 0;
    list-style: none;
    &_item {
        border-right: 2px dotted black;
        padding-right: 28px; }
    &_link {
        color: #fff;
        font-size: 15px;
        font-weight: 300; } }

.subheader {
    position: absolute;
    top: 50px;
    width: 100%;
    height: 84px;
    background-color: rgba(0, 0, 0, 0.6);
    &_logo {
        display: block;
        max-width: 100%;
        margin-top: 16px; }
    &_official {
        margin-top: 6px;
        opacity: 0.5;
        color: #ffffff;
        font-size: 13px;
        font-weight: 400; }
    &_call {
        margin-top: 19px;
        color: #ffffff;
        font-size: 12px;
        font-weight: 300;
        text-align: right; }
    &_phone {
        display: block;
        margin-top: 8px;
        color: #ffffff;
        font-size: 22px;
        font-weight: 400;
        text-transform: uppercase;
        text-align: right;
        line-height: 12px; }
    &_btn {
        margin-top: 19px;
        width: 172px;
        height: 46px;
        box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
        border: none;
        border-radius: 4px;
        background-image: linear-gradient(to top, $main-color 0%, #4ce2ff 100%);
        text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
        color: #ffffff;
        font-size: 14px;
        font-weight: 400;
        text-transform: uppercase; } }

.promo {
    min-height: 800px;
    background: url("../img/bg/main_bg.jpg") center (center / cover) no-repeat;
    padding: 150px 0 135px;
    &_header {
        color: #ffffff;
        font-size: 60px;
        font-weight: 700;
        text-transform: uppercase;
        line-height: 60px;
        margin: 0;
        text-align: center; }
    &_subheader {
        color: #ffffff;
        font-size: 36px;
        font-weight: 700;
        text-transform: uppercase;
        line-height: 36px;
        margin: 0;
        margin-top: 22px;
        text-align: center; }
    &_descr {
        color: #ffffff;
        font-size: 18px;
        font-weight: 400;
        line-height: 24px;
        text-align: center;
        margin-top: 23px; }
    &_btn {
        display: block;
        margin: 118px auto 0 auto;
        width: 259px;
        height: 63px;
        box-shadow: inset 0 -4px 0 rgba(0, 0, 0, 0.12);
        border: none;
        border-radius: 4px;
        background-image: linear-gradient(to top, $main-color 0%, #4ce2ff 100%);
        text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
        color: #ffffff;
        font-size: 18px;
        font-weight: 400;
        text-transform: uppercase; } }

.reasons {
    padding: 71px 0 65px 0;

    &_block {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 40px;
        &_round {
            display: flex;
            justify-content: center;
            align-items: center;
            margin-right: 26px;
            width: 113px;
            height: 113px;
            background-color: $main-color;
            border-radius: 100%; }
        &_descr {
            width: 341px;
            &_subtitle {
                line-height: 21px;
                color: $main-color;
                font-size: 17px;
                font-weight: 400; }
            &_text {
                color: #202020;
                font-size: 15px;
                font-weight: 300;
                line-height: 21px; } } } }

.mobile {
    min-height: 864px;
    padding: 48px 0 79px 0;
    background: url('../img/bg/mobile_app.png') center center/cover no-repeat;

    .row {
        margin-top: 32px; }

    &_item {
        position: relative;
        width: 320px;
        height: 280px;
        margin: 0 auto;
        margin-bottom: 19px;
        padding-top: 92px;
        &_1 {
            background: url('../img/bg/mobile_screen_1.jpg') center center/cover no-repeat; }
        &_2 {
            background: url('../img/bg/mobile_screen_2.jpg') center center/cover no-repeat; }
        &_3 {
            background: url('../img/bg/mobile_screen_3.jpg') center center/cover no-repeat; }
        &_4 {
            background: url('../img/bg/mobile_screen_4.jpg') center center/cover no-repeat; }
        &_5 {
            background: url('../img/bg/mobile_screen_5.jpg') center center/cover no-repeat; }
        &_6 {
            background: url('../img/bg/mobile_screen_6.jpg') center center/cover no-repeat; }
        &_subtitle {
            color: #ffffff;
            font-size: 24px;
            font-weight: 400;
            line-height: 31px;
            text-transform: uppercase;
            text-align: center; }
        &_plus {
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            bottom: 35px;
            left: 50%;
            transform: translateX(-50%);
            width: 26px;
            height: 26px;
            background-color: #000000;
            margin: 0 auto;
            border-radius: 100%;
            cursor: pointer; } } }

